/**
 * boolean true - true
 * @constant { Boolean } BOOLEAN_TRUE
 */
export const BOOLEAN_TRUE = true;

/**
 * boolean false - false
 * @constant { Boolean } BOOLEAN_FALSE
 */
export const BOOLEAN_FALSE = false;

/**
 * string empty - ''
 * @constant { String } STRING_EMPTY
 */
export const STRING_EMPTY = '';

/**
 * string true - true
 * @constant { String } STRING_TRUE
 */
export const STRING_TRUE = 'true';

/**
 * string false - false
 * @constant { String } STRING_FALSE
 */
export const STRING_FALSE = 'false';

/**
 * undefined
 * @constant { Undefined } UNDEFINED
 */
export const UNDEFINED = undefined;

/**
 * null
 * @constant { Null } NULL
 */
export const NULL = null;

/**
 * zero - 0
 * @constant { Number } NUMBER_ZERO
 */
export const NUMBER_ZERO = 0;
