/**
 * isSvg generator - isSvg
 * @param  { String } isSvg
 * @return { Boolean }
 */
const genIsSvg = () => {
  const reg = /(\.svg)$/;

  return (src) => {
    return reg.test(src);
  };
};

/**
 * is svg - svg
 * @param  { String } isSvg
 * @return { Boolean }
 */
export const isSvg = genIsSvg();
